.binary-tree {
    display: flex;
    justify-content: center;
  }
  
  .node {
    border: 1px solid black;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .child {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  
  .child:first-child {
    margin-right: 10px;
  }
  
  .child:last-child {
    margin-left: 10px;
  }
  